@import url("https://fonts.googleapis.com/css?family=Abril+Fatface|Poppins:400,500,600|Yeseva+One&display=swap");
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

html {
  scroll-behavior: smooth;
}

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.2;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  color: #5b5e5e;
}

body.no-scroll {
  overflow: hidden;
}

input, textarea {
  border: #666 1px solid;
  outline: none;
}

input:focus:required:invalid, textarea:focus:required:invalid {
  border-color: red;
}

input:required:valid, textarea:required:valid {
  border-color: green;
}

b, strong {
  font-weight: bold;
}

a {
  text-decoration: none;
}

.text-center {
  text-align: center;
}

.container {
  max-width: 1200px;
  padding: 0 30px;
  margin: 0 auto;
}

.container-header {
  max-width: 1335px;
  padding: 0 30px;
  margin: 0 auto;
}

.blue-link {
  color: #448ccb;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0.7px;
}

.semi-bold {
