}

.overlay {
  position: fixed;
  z-index: 10000;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fde3a7;
  height: 100vh;
}

.overlay__text {
  position: absolute;
  top: 50%;
  left: 50%;
  font-weight: 300;
  font-family: "Yeseva One", cursive;
  transform: translate(-50%, -50%);
  font-size: 60px;
}

.btn {
  display: inline-block;
  height: 64px;
  line-height: 64px;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.28px;
  cursor: pointer;
  text-decoration: none;
  background: none;
  outline: none;
}

.btn--fill {
  color: #3d3c3c;
  background-color: #fff;
  padding: 0 45px;
}

.btn--transparent {
  color: #fff;
  height: 64px;
  border: 2px solid #fff;
  transition: background-color 0.3s, color 0.3s;
}

.btn--transparent:hover, .btn--transparent:focus {
  background-color: #ffffff;
  color: #3d3c3c;
}

.back-bg::before {
  content: '';
  position: absolute;
  z-index: -3;
  bottom: -10px;
  right: -10px;
  width: 100%;
  height: 100%;
  display: block;
  background-color: #fde3a7;
}

.header {
  position: absolute;
  z-index: 3000;
  top: 0;
  left: 0;
  width: 100%;
  padding: 60px 0;
  transition: all 0.3s 1.3s;
}

.header.active {
  background-color: #fde3a7;
  transition-delay: 0s;
}

.header.active .header__logo {
  opacity: 0;
}

@media screen and (max-width: 650px) {
