}

.header__logo {
  transition: opacity 0.3s;
}

.burger-button {
  position: relative;
  display: none;
  border: none;
  background: none;
  padding: 0;
  width: 35px;
  height: 24px;
}

.burger-button__line, .burger-button::before, .burger-button::after {
  position: absolute;
  display: block;
  right: 0;
  height: 2px;
  background-color: #fde3a7;
  transition: background-color 0.3s 1.3s;
}

.burger-button::before, .burger-button::after {
  content: "";
}

.burger-button::before {
  top: 0;
  width: 100%;
}

.burger-button__line {
  top: 50%;
  width: 60%;
  margin-top: -1px;
}

.burger-button::after {
  width: 35%;
  bottom: 0;
}

.burger-button.active::before,
.burger-button.active .burger-button__line, .burger-button.active::after {
  transition-delay: 0s;
  background-color: #3d3c3c;
}

@media screen and (max-width: 650px) {
  .burger-button {
    display: block;
  }
}

@media screen and (max-width: 650px) {
  .header-nav {
    display: none;
  }
}

.header-nav__list {
  display: flex;
}

.header-nav__item:not(:last-child) {
  margin-right: 50px;
}

.header-nav__link {
  position: relative;
  padding: 5px 0;
  color: #fde3a7;
  font-family: inherit;
  font-size: 13px;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.33px;
  outline: none;
}

.header-nav__link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #fde3a7;
  transform: scaleX(0);
  transform-origin: 100% 50%;
  opacity: 0;
  transition: opacity 0.3s, transform 0.3s;
}

.header-nav__link:hover::before, .header-nav__link:focus::before {
  transform-origin: 0% 50%;
  transform: scaleX(1);
  opacity: 1;
}

.mobile-nav {
  display: none;
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  margin-top: -2px;
  padding: 30px;
  background-color: #fde3a7;
  transition: all 0.3s 1s ease-out;
  transform-origin: 50% 0%;
  transform: scaleY(0);
  opacity: 0;
  pointer-events: none;
}

.mobile-nav.active {
  opacity: 1;
  transition-delay: 0.3s;
  transform: scaleY(1);
  pointer-events: all;
}

.mobile-nav.active .mobile-nav__link {
  transform: translateX(0);
  opacity: 1;
  pointer-events: all;
  transition-timing-function: cubic-bezier(0.04, 0.96, 0.58, 1.03);
}

.mobile-nav.active .mobile-nav__link--1 {
  transition-delay: 1s;
}

.mobile-nav.active .mobile-nav__link--2 {
  transition-delay: 0.9s;
}

.mobile-nav.active .mobile-nav__link--3 {
  transition-delay: 0.8s;
}

.mobile-nav.active .mobile-nav__link--4 {
  transition-delay: 0.7s;
}

.mobile-nav.active .mobile-nav__link--5 {
  transition-delay: 0.6s;
}

@media screen and (max-width: 650px) {
  .mobile-nav {
    display: block;
  }
}

.mobile-nav__link {
  display: block;
  padding: 15px 0;
  font-size: 30px;
  color: #3d3c3c;
  opacity: 0;
  transform: translateX(-100%);
  pointer-events: none;
  transition: all 0.3s ease-in;
}

.mobile-nav__link--1 {
  transition-delay: 0.4s;
}

.mobile-nav__link--2 {
  transition-delay: 0.5s;
}

.mobile-nav__link--3 {
  transition-delay: 0.6s;
}

.mobile-nav__link--4 {
  transition-delay: 0.7s;
}

.mobile-nav__link--5 {
  transition-delay: 0.8s;
}

.hero {
